import * as React from 'react';

class Loading extends React.Component<{}, {}> {
  constructor(props: {}) {
    super(props);
    this.state = {};
  }

  public render() {
    const styles = {
      position: 'fixed' as 'fixed',
      top: 0,
      right: 0,
      bottom: 0,
      left: 0,
      margin: 'auto',
      textAlign: 'center' as 'center',
      color: '#000000',
      width: '96px',
      height: '96px',
      background:
        'url("data:image/svg+xml;base64,") 50% 50% no-repeat',
      backgroundSize: '100%',
    };

    return <figure style={styles} />;
  }
}

export default Loading;
